<template>
    <div class="cities">
        <app-container>
            <ul class="cities__list">
                <city-item
                    v-for="city, index in cities"
                    :key="index"
                    :city="city"
                />
            </ul>
            <div v-intersection="loadMoreCities" class="intersection-el"></div>
        </app-container>
    </div>
</template>
<script>
import CityItem from '@/components/CityItem'
import AppContainer from '@/components/AppContainer'
import {mapActions, mapMutations, mapState} from 'vuex'
export default {
    components: {
        AppContainer,
        CityItem,
    },

    computed: {
        ...mapState({
            citesAtOnce: state => state.cities.citesAtOnce,
            cities: state => state.cities.cities,
        })
    },
    methods: {
        ...mapActions({
            loadMoreCities: 'cities/loadMoreCities'
        }),
        ...mapMutations({
            setCities: 'cities/setCities',
            setLoadedCities: 'cities/setLoadedCities'
        })
    },
    mounted() {
        this.loadMoreCities()  

    },
}
</script>
<style>
    .cities__list {
        font-weight: bold;
        color: #5a7fc2;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }

    .intersection-el {
        width: 100%;
        height: 20px;
    }
</style>